.wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .pages {
    margin: 0 10px;
    cursor: pointer;
  }
  .selectPage {
    font-weight: 600;
    color: rgb(255, 87, 87);
  }

  .btn {
    border: none;
    background: none;
    font-size: 18px;
    cursor: pointer;
  }
  .btn:disabled {
    cursor: default;
  }
}
